import axios from 'axios';
import React from 'react';
import { useState, useEffect } from "react";
import Slider from '@mui/material/Slider';
import { HexColorPicker } from "react-colorful";
import TextField from '@mui/material/TextField';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import TimePicker from '@mui/lab/TimePicker';

import ServerHost from './../Server.jsx'
import RefreshRate from './../Refresh.jsx'

import "./../../styles/theme.css";
import "./../../styles/dashboard.css";

import LightOff from "./../../img/light-off.png"
import Light25 from "./../../img/light-25.png"
import Light50 from "./../../img/light-50.png"
import Light75 from "./../../img/light-75.png"
import Light100 from "./../../img/light-100.png"

const SmartLight = () => {
    const [lightColor, setLightColor] = useState("#aabbcc");
    const [lightValue, setLightValue] = useState(0);
    const [lightSensor, setLightSensor] = useState(0);
    const [bedtime, setBedtime] = useState(null);
    const [wakeup, setWakeup] = useState(null);

    const lightMarks = [
        {
            value: 0,
            label: '0%',
        },
        {
            value: 50,
            label: '50%',
        },
        {
            value: 100,
            label: '100%',
        }
    ];

    const UpdateParticle = async () => {

        const data = {
            smartlight: {
                brightness: lightValue
            }
        }

        const response = await axios.post(
            ServerHost + "/serial/write", 
            data
        );

        if (response.status === 201) {
            return;
        } else {
            console.log('Error while writing data');
        }
    }

    const AdjustLight = (sliderRead) => {
        setLightValue(sliderRead);

        var lightImg = document.getElementById('lightbulb');

        if (lightValue === 0) {
            lightImg.src = LightOff;
        } else if (lightValue < 25) {
            lightImg.src = Light25;
        } else if (lightValue < 50) {
            lightImg.src = Light50;
        } else if (lightValue < 75) {
            lightImg.src = Light75;
        } else {
            lightImg.src = Light100;
        }

        UpdateParticle();
    }

    useEffect(() => {
        const interval = setInterval(() => {
            const deviceReadings = localStorage.getItem('DeviceReadings');
            if (deviceReadings) {
                const readings = JSON.parse(deviceReadings);
                setLightSensor(readings.light);
            }
        }, RefreshRate);
        return () => clearInterval(interval);
    }, []);

    return (
        <div className="dashCompDiv">
            <div id="lightDiv">
                <h5> Smart Light </h5>
                <p> Light Sensor: {lightSensor} </p>

                <img id="lightbulb" src={LightOff} alt="lightbulb"/>

                <Slider
                  aria-label="Custom marks"
                  defaultValue={0}
                  step={1}
                  valueLabelDisplay="auto"
                  marks={lightMarks}
                  onChange={({ target }) => AdjustLight(target.value)}
                />

                <HexColorPicker 
                    id="colorSel"
                    color={lightColor} 
                    onChange={setLightColor}/>

                <div id="lightScheduleDiv">
                    <div className="timeSel">
                        <LocalizationProvider dateAdapter={AdapterDateFns}>
                          <TimePicker
                            className="lightTimePicker"
                            label="Bedtime"
                            value={bedtime}
                            onChange={(val) => {setBedtime(val)}}
                            renderInput={(params) => <TextField {...params} />}
                          />
                        </LocalizationProvider>
                    </div>

                    <div className="timeSel">
                        <LocalizationProvider dateAdapter={AdapterDateFns}>
                          <TimePicker
                            className="lightTimePicker"
                            label="Wake-up"
                            value={wakeup}
                            onChange={(val) => {setWakeup(val)}}
                            renderInput={(params) => <TextField {...params} />}
                          />
                        </LocalizationProvider>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default SmartLight;
