import React from 'react';
import { useState, useEffect } from "react";
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import OpenDoor from "./../../img/door-open.png"
import ClosedDoor from "./../../img/door-close.png";

import RefreshRate from './../Refresh.jsx'

import "./../../styles/theme.css";
import "./../../styles/dashboard.css";

const Door = () => {

    const [doorClosed, setDoorStatus] = useState(false);
    const [doorSensor, setDoorSensor] = useState(0);

    const AdjustDoor = () => {
        const doorImg = document.getElementById('door');

        if (!doorClosed) {
            localStorage.setItem('Door', '0');
            doorImg.src = OpenDoor;
        } else{
            localStorage.removeItem('Door');
            doorImg.src = ClosedDoor;
        }
        
        setDoorStatus(!doorClosed);
    }

    useEffect(() => {
        const interval = setInterval(() => {
            const deviceReadings = localStorage.getItem('DeviceReadings');
            if (deviceReadings) {
                const readings = JSON.parse(deviceReadings);
                setDoorSensor(readings.door);
            }

            const doorTimer = localStorage.getItem('Door');
            if (doorTimer) {
                if (doorTimer === '0') {
                    localStorage.setItem('Door', '1');
                } else if (doorTimer === '1') {
                    localStorage.setItem('Door', '2');
                } else if (doorTimer === '2') {
                    alert('The door is still open');
                    localStorage.setItem('Door', '0');
                }
            }
        }, RefreshRate);
        return () => clearInterval(interval);
    }, []);

    return (
        <div className="dashCompDiv">
            <div id="doorDiv">
                <h5> Door </h5>
                <p> Door Sensor: {doorSensor} </p>

                <img id="door" src={ClosedDoor} alt="door"/>

                <FormControlLabel 
                    id="doorLabel"
                    control={<Switch/>} 
                    label=''
                    value={doorClosed}
                    onChange={() => AdjustDoor()}
                />
                
            </div>
        </div>
    );
}

export default Door;
