import React from 'react';
import { useState, useEffect } from "react";
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';

import RefreshRate from './../Refresh.jsx'

import "./../../styles/theme.css";
import "./../../styles/dashboard.css";

const thermostatInit = {
    systemMode : 'off',
    fanMode    : 'auto',
    temp       : 76, 
    curTemp    : '--',
    humidity   : '--',
    status     : 'off'
}

const Thermostat = () => {

    const [thermostat, setThermostat] = useState(thermostatInit);

    const updateThermostat = (key, value) => {
        setThermostat(prevThermo => ({
            ...prevThermo,
            [key]: value
        }));
    };

    useEffect(() => {
        const interval = setInterval(() => {
            const deviceReadings = localStorage.getItem('DeviceReadings');
            if (deviceReadings) {
                const readings = JSON.parse(deviceReadings);
                if (('temp' in readings) && ('humidity' in readings)) {
                    updateThermostat('curTemp', readings.temp);
                    updateThermostat('humidity', readings.humidity);

                    if ((readings.temp < thermostat.temp && thermostat.systemMode == 'heat') || 
                        (readings.temp > thermostat.temp && thermostat.systemMode == 'cool')) {
                        updateThermostat('status', 'on');
                    } else {
                        updateThermostat('status', 'off');
                    }
                } 
            }
        }, RefreshRate);
        return () => clearInterval(interval);
    }, []);

    return (
        <div className="dashCompDiv">
            <div id="tempDiv">
                <h5> Thermostat </h5>
        
                <div id="currentTempData">
                    <p id="curTemp">{thermostat.curTemp}Â°F</p>
                    <p id="curHum">Humidity: {thermostat.humidity}%</p>
                    <p id="curHum">Status: {thermostat.status}</p>
                </div>

                <FormControl sx={{ m: 1, minWidth: 150 }}>
                    <InputLabel id="demo-simple-select-helper-label">System Mode</InputLabel>
                    <Select
                        labelId="demo-simple-select-helper-label"
                        id="demo-simple-select-helper"
                        value={thermostat.systemMode}
                        label="System Mode"
                        onChange={({ target }) => updateThermostat('systemMode', target.value)}
                        >
                        <MenuItem value={'off'}>Off</MenuItem>
                        <MenuItem value={'cool'}>Cool</MenuItem>
                        <MenuItem value={'heat'}>Heat</MenuItem>
                      </Select>
                </FormControl>

                <FormControl sx={{ m: 1, minWidth: 120 }}>
                    <InputLabel id="demo-simple-select-helper-label">Fan Mode</InputLabel>
                    <Select
                        labelId="demo-simple-select-helper-label"
                        id="demo-simple-select-helper"
                        value={thermostat.fanMode}
                        label="Fan Mode"
                        onChange={({ target }) => updateThermostat('fanMode', target.value)}
                        >
                        <MenuItem value={'on'}>On</MenuItem>
                        <MenuItem value={'auto'}>Auto</MenuItem>
                      </Select>
                </FormControl>

                <div>
                    <TextField
                        id="tempEntry" 
                        label="Temperature"
                        value={thermostat.temp}
                        InputProps={{ inputProps: { 
                            min: 68, 
                            max: 86, 
                            onKeyDown: (event) => { // disable typing
                                event.preventDefault();
                            }
                        }}}
                        type="number"
                        onChange={({ target }) => updateThermostat('temp', target.value)}
                        margin="normal"
                    />
                </div>

            </div>
        </div>
    );
}

export default Thermostat;
