/**
 * Contain all the Particle connection requests and data 
 * population
 *
 * @file      ParticleConnect.jsx.
 * @author    Martin Lopez, Diego Moscoso, Jacob Summerville.
 * @since     11/29/2021
 * @copyright Martin Lopez, Diego Moscoso, Jacob Summerville 2021
 */

import axios from 'axios';
import React, { useState, useEffect } from "react";
import Button from '@mui/material/Button';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';

import ServerHost from './../Server.jsx'
import RefreshRate from './../Refresh.jsx'

import "./../../styles/theme.css";
import "./../../styles/dashboard.css";

const ParticleConnect = () => {

    const [portList, setPortList] = useState([]);
    const [portSel,  setPortSel]  = useState('');
    const [btnColor, setBtnColor] = useState('success');
    const [btnLabel, setBtnLabel] = useState('Connect');

    const ScanPorts = async () => {
        const response = await axios.post(
            ServerHost + "/serial/scan",
            { cmd: "scan" }
        );

        if (response.status === 201) {

            setPortList(response.data.list);

            const device = localStorage.getItem('Device');
            if (device) {
                const deviceInfo = JSON.parse(device);
                if (response.data.list.includes(deviceInfo.port)) {
                    setBtnLabel('Disconnect');
                    setBtnColor('error');
                    setPortSel(deviceInfo.port);
                }
            } 
        }
    }

    const ReadData = async () => { 
        const response = await axios.post(
            ServerHost + "/serial/read",
            { cmd: "read" }
        );

        if (response.status === 201) {
            const deviceReadings = {
                temp     : response.data.data.temp,
                humidity : response.data.data.humidity,
                light    : response.data.data.light,
                door     : response.data.data.door,
                simclock : response.data.data.simclock
            }
            localStorage.setItem('DeviceReadings', JSON.stringify(deviceReadings));
        } else {
            alert('Unable to read data');
        }
    }

    useEffect(() => {
        ScanPorts();

        const interval = setInterval(() => {
            const device = localStorage.getItem('Device');
            if (device) {
                ReadData();
            }
        }, RefreshRate);
        return () => clearInterval(interval);
    }, []);

    const ConnectDevice = async () => {
        if (btnLabel === 'Connect') {

            if (portSel === '') {
                alert('Please select a port');
                return;
            }

            const response = await axios.post(
                ServerHost + "/serial/open",
                { cmd: "open", path: portSel }
            );

            if (response.status === 200) {
                setBtnLabel('Disconnect');
                setBtnColor('error');
                localStorage.setItem('Device', JSON.stringify({'port': portSel}));
            } else if (response.status === 201){
                alert('Unable to connect! Resource busy');
            } else {
                alert('Unable to connect! There was an unknown error');
            }
        } else {
            const response = await axios.post(
                ServerHost + "/serial/close",
                { cmd: "close" }
            );

            if (response.status === 201) {
                setBtnLabel('Connect');
                setBtnColor('success');
                localStorage.removeItem('DeviceReadings');
                localStorage.removeItem('Device');
            } else {
                alert('Unable to disconnect');
            }
        }
    }

    return (
        <div id="deviceConnect">
            <FormControl sx={{ m: 1, minWidth: 250 }}>
                <InputLabel id="demo-simple-select-helper-label">Select your port</InputLabel>
                <Select
                    labelId="demo-simple-select-helper-label"
                    id="demo-simple-select-helper"
                    value={portSel}
                    label="Select your port"
                    onChange={({ target }) => setPortSel(target.value)}
                    >
                        {portList?.map(port => {
                            return (
                                <MenuItem key={port} value={port}>
                                    {port ?? port}
                                </MenuItem>
                            );
                        })}
                </Select>
            </FormControl>
            <Button 
                sx={{ m: 2 }}
                color={btnColor}
                variant="contained" 
                size="small"
                onClick={ConnectDevice}>
                {btnLabel}
            </Button>
        </div>
    );
}

export default ParticleConnect;
