import React, { useState, useEffect } from "react";
import { Box, Container, Grid } from '@mui/material';

import { DashboardLayout } from './../components/dashboard/dashboard-layout.jsx';
import ParticleConnect from './../components/dashboard/ParticleConnect.jsx';
import SmartLight      from './../components/dashboard/SmartLight.jsx';
import Thermostat      from './../components/dashboard/Thermostat.jsx';
import Door            from './../components/dashboard/Door.jsx';

import RefreshRate from './../components/Refresh.jsx'

import "./../styles/theme.css";
import "./../styles/dashboard.css";

const TimeDisplay = (props) => {
    if (props.date === '00:00') {
        return ( <p id="timeDisp">{'00:00'}</p> );
    }
    var hours = props.date.getHours();
    var minutes = props.date.getMinutes();
    var ampm = hours >= 12 ? 'PM' : 'AM';
    hours = hours % 12;
    hours = hours ? hours : 12; // the hour '0' should be '12'
    minutes = minutes < 10 ? '0'+minutes : minutes;
    var strTime = hours + ':' + minutes + ' ' + ampm;
    return ( <p id="timeDisp">{strTime}</p> );
}

const Dashboard = () => {
    const [time, setTime] = useState('00:00');

    useEffect(() => {
        const interval = setInterval(() => {
            const deviceReadings = localStorage.getItem('DeviceReadings');
            if (deviceReadings) {
                const readings = JSON.parse(deviceReadings);
                const simDate = new Date(readings.simclock);
                setTime(simDate);
            }
        }, RefreshRate);
        return () => clearInterval(interval);
    }, []);

    return (
        <div id="dashContainer">
            <TimeDisplay date={time}/>

            <ParticleConnect/>

            <Box component="main" id="dashboardBox" sx={{ flexGrow: 1, py: 8 }}>
                <Container maxWidth={false}>
                    <Grid container spacing={3}>
                        <Grid item xl={4} lg={4} sm={6} xs={12}>
                            <Door />
                        </Grid>

                        <Grid item xl={4} lg={4} sm={6} xs={12}>
                            <Thermostat sx={{ height: '100%' }} />
                        </Grid>

                        <Grid item xl={4} lg={4} sm={6} xs={12}>
                            <SmartLight />
                        </Grid>
                    </Grid>
                </Container>
            </Box>
        </div>
    )
};

Dashboard.getLayout = (page) => (
    <DashboardLayout>
        {page}
    </DashboardLayout>
);

export default Dashboard;